/**
 * Notice for cookies
 */
#cookie-notice {
  padding: 1em 1.8em;
  overflow: hidden;
  box-sizing: border-box;
  display: none;
  font-size: 16px;
  font-family: Helvetica,Calibri,Arial,sans-serif;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  background: #030954;
  opacity: 0.8;
  color: #ffffff;
  flex-wrap: nowrap;
  flex-direction: row;
  z-index: 9999;
}

.cookie-message {
  display: block;
  -webkit-box-flex: 1;
  flex: 1 1 auto;
  max-width: 100%;
  margin-right: 1em;
}

.cookie-compliance {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-line-pack: justify;
  align-content: space-between;
}

.cookie-more-link {
  opacity: .8;
  display: inline-block;
  cursor: pointer;
  padding: .2em;
  text-decoration: underline;
}
.cookie-more-link:visited, .cookie-more-link, .cookie-more-link:hover {
  color: #ffffff
}

.cookie-btn {
  display: block;
  padding: .4em .8em;
  font-size: .9em;
  font-weight: 700;
  border-width: 2px;
  border-style: solid;
  text-align: center;
  white-space: nowrap;
  margin-left: .2em;
}

.cookie-btn, .cookie-revoke {
  cursor: pointer;
}

.cookie-allow {
  color: _palette(logo-blue);
  border-color: transparent;
  background-color: _palette(logo-yellow);
}

.cookie-allow:hover, .cookie-allow:focus {
  background-color: rgb(255, 190, 41);
  color:  rgb(3, 9, 122);
}

.cookie-deny {
  color: #ffffff;
  background-color: transparent;
  border-color: transparent;
}
.cookie-deny:hover, .cookie-deny:active {
  color: #ffffff;
  background-color: transparent;
  text-decoration: underline;
}

.cookie-revoke {
  bottom: -2em;
  transition: 0.3s; /* Add transition on hover */
  left: 3em;
  border-top-left-radius: .5em;
  border-top-right-radius: .5em;
  position: fixed;
  padding: .5em;
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: Helvetica,Calibri,Arial,sans-serif;
  font-size: 16px;
  line-height: 1.5em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: none;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  z-index: 9999;
  color: rgb(255, 255, 255);
  background-color: rgb(3, 9, 84);
}

.cookie-revoke:hover {
  bottom: 0;
  text-decoration: underline;
}

@media only screen and (max-width: 800px) {
  .cookie-revoke {
    bottom: 0;
  }

  #cookie-notice {
    flex-direction: column;
  }
}
