.youtube-embed {
  position: relative;
}

.youtube-embed__notice {
  width: 100%;

  img {
    z-index: 1;
    min-width: 100%;
    min-height: 100%;
    opacity: 0.4;
  }

  p {
    color: _palette(logo-blue);
    background-color: _palette(logo-yellow);
    opacity: 0.8;
    font-size: 1.5em;
    position: absolute;
    top: 50%;
    text-align: center;
    min-width: 100%;
    z-index: 2;
  }
}

.youtube-embed__iframe {
  display: none;
  width: 100%;
  height: 100%;
}

.cookie-accepted {

  .youtube-embed__notice {
    display: none;
  }

  .youtube-embed__iframe {
    display: block;
  }
}
